<template>
    <view class="w-select">
        <view 
        class="w-select__content"
        :style="'width: ' + width + ';'"
        :class="{'w-select__content-active': show}"
        @click="$emit('isActive')">
            <slot></slot>
            <i class="w_editor-icon w_editor-icon-sorting"></i>
        </view>
        <view class="w-select__popup" :style="'width: ' + width + ';'" v-if="show">
            <view 
            class="w-select__popup-option" 
            v-for="(item, i) in options" 
            :key="i"
            @click="$emit('change', item.value)">
                <template v-if="type == 'textAlign'">
                    <i class="w_editor-icon" :class="item.label"></i>
                </template>
                <template v-else-if="type == 'color'">
                    <view
                    class="color-box" 
                    :style="'background:' + item.label">
                    </view>
                </template>
                <template v-else>
                    {{item.label}}
                </template>
            </view>
        </view>
        
        <view 
        v-if="show" 
        class="w-select__modal" 
        @click="$emit('update:show',false)">
        </view>
    </view>
</template>

<script>
    export default {
        props: {
            show: {
                type: Boolean,
                default: false,
            },
            options: {
                type: Array,
                default: [],
            },
            type: {
                type: String,
                default: 'text',
            },
            width: {
                type: String,
                default: '90rpx',
            }
        }
    }
</script>

<style lang="scss" scoped>
    
    .w-select{
        margin-right: 10rpx;
        &__content{
            box-sizing: border-box;
            padding: 0 10rpx;
            height: 50rpx;
            display: flex;
            justify-content: space-between;
            align-items: center;
            &-active{
                border: 1px solid #bbb;
            }
        }
        &__popup{
            z-index: 20001;
            position: absolute;
            background-color: #fff;
            box-shadow: 2rpx 2rpx 10rpx #bbb;
            padding: 10rpx;
            &-option{
                text-align: center;
                line-height: 46rpx;
            }
        }
        &__modal{
            z-index: 20000;
            position: fixed;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
        }
        
        .color-box{
            width: calc(100% - 20rpx);
            height: 40rpx;
            margin: 0 10rpx;
            display: inline-block;
        }
    }
</style>